import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

//  

//  
const AnimePreloader = () => {

  setTimeout(()=> {
    document.querySelector('body').classList.remove('anime-block')
    document.querySelector('.preloader').classList.remove('anime-visible')

    setTimeout(()=>{
      document.querySelector('.preloader').classList.add('anime-hidden')
    },1000)

  },3000)

}


//   , 
if(document.cookie){
  AnimePreloader();
} else{
  document.cookie = "user=ghost";
}




