import './development.scss';

import svgDev from '../../images/developt.svg'
import svgCode from '../../images/Code.svg'
import svgPaint from '../../images/paint.svg'

import svgReact from '../../images/card/React.svg'
import svgRedux from '../../images/card/Redux.svg'
import svgTS from '../../images/card/Typescript.svg'

import svgJS from '../../images/card/NodeJs.svg'
import svgHtml from '../../images/card/Html.svg'
import svgCss from '../../images/card/Css.svg'
import svgScss from '../../images/card/Scss.svg'
import svgPug from '../../images/card/Pug.svg'

import svgWordpress from '../../images/card/Wordpress.svg'
import svgRest from '../../images/card/Rest.svg'
import svgGraohQl from '../../images/card/GraphQL.svg'

import svgDocker from '../../images/card/Docker.svg'

import svgGithub from '../../images/card/Github.svg'
import svgGit from '../../images/card/Git.svg'
import svgGitlab from '../../images/card/Gitlab.svg'

import svgRocket from '../../images/card/advantages/rocket.svg'
import svgLamp from '../../images/card/advantages/lamp.svg'
import svgSpeed from '../../images/card/advantages/speed.svg'
import svgLapt from '../../images/card/advantages/laptop.svg'


function Development() {

    return <div className="development">

        <img className='svg svg-dev' src={svgDev} alt="fireSpot"/>

        <div className='development__inner'>

            <div className='development__inner-head'>

                <div className="robocat">
                    <canvas id="canvas"></canvas>
                </div>


                <div className='development-info'>

                    <div className='development-info__head'>

                        <img className='svg svg-code' src={svgCode} alt="fireSpot"/>
                        <div className="title title_type-2 development-info__title">Development</div>
                        <img className='svg svg-paint' src={svgPaint} alt="fireSpot"/>

                    </div>

                    <div className="development__tabs">

                        <a className='btn btn_type-1 btn--active' href="#">
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                            Website
                        </a>

                        <a className='btn btn_type-1' href="#">
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                            Design
                        </a>

                        <a className='btn btn_type-1' href="#">
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                            Modification
                        </a>

                    </div>

                </div>
                
            </div>

            <div className='technologies padding-page'>

                <div className='technologies-info'>
                    <div className="title title_type-2">Technologies</div>
                    <div className="technologies-info__subtitle">I use the following technologies to develop a website</div>
                </div>

                <div className='technologies__inner'>

                    <div className='card card_type-2'>

                        <div className="card__circle">

                            <img className='svg svg-react' src={svgReact} alt="fireSpot"/>
                            <img className='svg svg-redux' src={svgRedux} alt="fireSpot"/>
                            <img className='svg svg-ts' src={svgTS} alt="fireSpot"/>

                        </div>

                        <div className="card__info">

                            <div className="card__title">React + Redux <br/> Typescript</div>

                        </div>

                    </div>

                    <div className='card card_type-2'>

                        <div className="card__circle">

                            <img className='svg svg-js' src={svgJS} alt="fireSpot"/>
                            <img className='svg svg-html' src={svgHtml} alt="fireSpot"/>
                            <img className='svg svg-css' src={svgCss} alt="fireSpot"/>
                            <img className='svg svg-scss' src={svgScss} alt="fireSpot"/>
                            <img className='svg svg-pug' src={svgPug} alt="fireSpot"/>

                        </div>

                        <div className="card__info">

                            <div className="card__title">Html5(Pug) Css3(Scss) Javascript(ES6+)</div>

                        </div>

                    </div>

                    <div className='card card_type-2'>

                        <div className="card__circle">

                            <img className='svg svg-wordpress' src={svgWordpress} alt="fireSpot"/>
                            <img className='svg svg-rest' src={svgRest} alt="fireSpot"/>
                            <img className='svg svg-graphql' src={svgGraohQl} alt="fireSpot"/>

                        </div>

                        <div className="card__info">

                            <div className="card__title">Wordpress <br/> Rest Api <br/> GrapgQL</div>

                        </div>

                    </div>

                    <div className='card card_type-2'>

                        <div className="card__circle">

                            <img className='svg svg-docker' src={svgDocker} alt="fireSpot"/>

                        </div>

                        <div className="card__info">

                            <div className="card__title">Docker</div>

                        </div>

                    </div>

                    <div className='card card_type-2'>

                        <div className="card__circle">

                            <img className='svg svg-gitlab' src={svgGitlab} alt="fireSpot"/>
                            <img className='svg svg-git' src={svgGit} alt="fireSpot"/>
                            <img className='svg svg-github' src={svgGithub} alt="fireSpot"/>

                        </div>

                        <div className="card__info">

                            <div className="card__title">Git - Github - GitLab</div>

                        </div>

                    </div>

                </div>

            </div>

            <div className="advantages">

                <img className='svg svg-advantages advantages__item' src={svgLapt} alt="fireSpot"/>
                <img className='svg svg-advantages advantages__item' src={svgSpeed} alt="fireSpot"/>
                <img className='svg svg-advantages advantages__item' src={svgLamp} alt="fireSpot"/>
                <img className='svg svg-advantages advantages__item' src={svgRocket} alt="fireSpot"/>

            </div>

        </div>

        <img className='svg svg-dev svg-dev_reverse' src={svgDev} alt="fireSpot"/>

    </div>;

}
export default Development;