import './portfolio.scss';

import title from '../../images/title.svg'
import Card from '../card/card_type-1'

function Portfolio() {

    return <div className="portfolio">

        <div className="title title_type-1">

            <div className="title__text">Portfolio</div>
            <img className='svg svg-title' src={title} alt="fireSpot"/>

        </div>

        <div className='portfolio__cards'>

            <Card title="TaigaBarber" subtitle="Website" img=""/>
            <Card title="Shor-vityaz35" subtitle="Website" img=""/>
            <Card title="5s-proekt.ru" subtitle="Website" img=""/>

        </div>
        
    </div>;

}
export default Portfolio;