import './blog.scss';

import title from '../../images/title.svg'

function Blog() {

    return <div className="blog">

        <div className="title title_type-1">

            <div className="title__text">Blog</div>
            <img className='svg svg-title' src={title} alt="fireSpot"/>

        </div>

        <div className='blog__cards'>

            <div className="card card_type-3">
                {/* <div className="card__num">01</div> */}
                <a href='/' className="card__link">
                    <div>
                        <h1>HTML Syntax</h1>
                        <p>The syntax of a language is how it works. How to actually write it. Learn HTML syntax</p>
                        <div className="date">6 Oct 2017</div>
                        <div className="tags">
                        <div className="tag">HTML</div>
                        </div>
                    </div>
                </a>
            </div>

        </div>
        
    </div>;

}

export default Blog;