
function Card(props) {

    return <div className='card card_type-1'>

        <div className="card__avatar">
            <div className='card__avatar-image'>
                {/* <img className='svg svg-node' src={svgNodeJs} alt="fireSpot"/> */}
            </div>
        </div>

        <div className='card__info'>

            <div className='card__info-wrap'>

                <div className='card__cup'></div>

                <div className='card__info-title'>{props.title}<br />
                    <span class="card__info-subtitle"> {props.subtitle}</span>
                </div>
                
                <div className='card__date'>

                    <h3> <span className='card__date-text'>1</span>text</h3>
                    <h3> <span className='card__date-text'>2</span>text</h3>
                    <h3> <span className='card__date-text'>3</span>text</h3>

                </div>

                <a href='/' className='card__button btn btn_type-1'>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    View
                </a>

            </div>

        </div>

    </div>

}
export default Card;