import './banner.scss';
import svgBanner from '../../images/banner-back_ultra.svg'

import svgReact from '../../images/React.svg'
import svgVue from '../../images/Vue.svg'
import svgPug from '../../images/Pug.svg'
import svgNodeJs from '../../images/NodeJs.svg'
import svgHtml from '../../images/Html.svg'
import svgCss from '../../images/Css.svg'
import svgGitlab from '../../images/Gitlab.svg'
import svgGithub from '../../images/Github.svg'
import svgDocker from '../../images/Docker.svg'


function Banner() {

    return <div className="banner">

        <nav className="menu menu_header">

            <a href="/" className="menu-item">Home</a>
            <a href="/" className="menu-item">Portfolio</a>
            <a href="/" className="menu-item">Contact</a>
            <a href="/" className="menu-item">Blog</a>

        </nav>

        <div className='description'>

            <div className='description__title'>Web Developer</div>
            <div className='description__subtitle'>Hello, Im <i>Aleksey Gorbunov</i></div>

        </div>

        <div className="parallax">

            <img className='svg svg-react' src={svgReact} alt="fireSpot"/>
            <img className='svg svg-vue' src={svgVue} alt="fireSpot"/>
            <img className='svg svg-pug' src={svgPug} alt="fireSpot"/>
            <img className='svg svg-node' src={svgNodeJs} alt="fireSpot"/>

            <div className="parallax__group">

                <img className='svg svg-html' src={svgHtml} alt="fireSpot"/>
                <img className='svg svg-css' src={svgCss} alt="fireSpot"/>

            </div>

            <img className='svg svg-gitlab' src={svgGitlab} alt="fireSpot"/>
            <img className='svg svg-github' src={svgGithub} alt="fireSpot"/>
            <img className='svg svg-docker' src={svgDocker} alt="fireSpot"/>

        </div>


        <img className='svg svg_banner-back' src={svgBanner} alt="fireSpot"/>
        
    </div>;

}
export default Banner;